#GenerateArrayButton {
    background-color: rgba(255,255,255,.05);
    padding: 5px 20px;
    border-radius: 10px;
    cursor: pointer;
}
@media screen and (max-width: 900px) {
    #GenerateArrayButton h1 {
        display: none;
    }
    #GenerateArrayButton {
        padding: 5px !important;
    }
}